h1, h2, p {
  font-family: Arial;
}

h2 {
  font-weight: normal;
}

.chart {
  max-width: 500px;
  width: 100%;
  height: 600px;
  display: flex;
  position: relative;
}

.chart svg {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
}

.chart svg line {
  stroke-width: 2px;
}

.chart svg .surface-line {
  stroke: #000;
}

.chart svg .paper {
  stroke: gray;
}

.chart svg .start-angle {
  stroke: #4682b4;
}

.chart svg .reflect-angle {
  stroke: tomato;
}

.chart svg .start-handle {
  stroke: #fff;
  cursor: move;
  fill: #4682b4;
}

.chart svg .start-handle:hover {
  fill: #205784;
}

.chart.intersected svg {
  background-color: #bbeabb;
}

